import React, { Component } from 'react';
import Button from './Button';
/*import axios from 'axios'*/

class Contact extends Component{
  constructor(props){
    super(props)
    this.state = {
      name:'',
      email:'',
      message:'',
      opacity: 0,
      focusLabel:null
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
  }
  componentDidMount(){
    const opacity = setInterval(()=>{
      this.setState({opacity:1})
      clearInterval(opacity);
    },100);
  }
  handleSubmit(event){
    event.preventDefault();
    const {name,email,msg} = this.state;
    alert(`Ime:${name}, E-adresa:${email}, Poruka: ${msg}`);
  /*  axios.get(`http://localhost:3000/index.php`,{
      data:{
        name:name,
        email:email,
        message: msg
      }

    }

  )
    .then((response)=> console.log(response))
    .catch(error => console.log(error))*/
  }
postSend(data){
  alert(data);
}
handleError(error){
  alert(error)
}
  handleChange(event){
      const stateName = event.target.name;
      this.setState({[stateName]:event.target.value })
  }

handleFocus(props){
  let name = props.target.name;
  this.setState({focusLabel:name})
}
handleBlur(porps){
  let targetName = porps.target.name;

}
  render(){
    const {name,email,message,focusLabel} = this.state;
    return(
    <div className='wrapper' style={{opacity:this.state.opacity}}>
      <form onSubmit={this.handleSubmit}>
        <div className='contact-form'>
          <div className='contact-form_group'>
            <label htmlFor='name' className={`contact-label ${this.state.focusLabel === 'name' ? ' move_label' : ''} ${this.state.name.length > 0 ? ' move_label' : ''} `} >Ime</label>
            <input onChange={this.handleChange} onFocus={this.handleFocus} onBlur={this.handleBlur}  value={this.state.name} className='contact-form_input' type='text' name='name'/>
          </div>
          <div className='contact-form_group'>
            <label htmlFor='email' className={`contact-label ${this.state.focusLabel === 'email' ? ' move_label' : ''} ${this.state.email.length > 0 ? ' move_label' : ''}`}>E-adresa</label>
            <input onChange={this.handleChange} onFocus={this.handleFocus} onBlur={this.handleBlur}  value={this.state.email} className='contact-form_input' type='email' name='email'/>
          </div>
          <div className='contact-form_group'>
            <label htmlFor='message' className={`contact-label ${this.state.focusLabel === 'message' ? ' move_label' : ''} ${this.state.message.length > 0 ? ' move_label' : ''}`}>Poruka</label>
            <textarea className='contact-form_textarea' cols='3' rows='8' onChange={this.handleChange} onFocus={this.handleFocus} onBlur={this.handleBlur} value={this.state.message} name='message'></textarea>
          <Button className='button' type='submit' children='Posalji' />
        </div>
        </div>
      </form>
    </div>
    )
  }
}
export default Contact;



